import { $ } from './utils';

export class FormHandler {
  /**
   * @typedef {function(string): void} OnSubmit
   * @param {Object} params
   * @param {OnSubmit} params.onSubmit - ÏïÑÏù¥ÌÖúÏù¥ Ï†úÏ∂úÎê† Îïå Ìò∏Ï∂úÎêòÎäî ÏΩúÎ∞± Ìï®Ïàò
   */
  constructor({ onSubmit }) {
    this.form = $('#todo-form');
    this.input = $('.input__component[data-type="creation"] > input');
    this.callback = onSubmit;

    // Ìèº Ï†úÏ∂ú Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà Îì±Î°ù
    this.#initEventListeners();
  }

  #initEventListeners() {
    if (!this.form) {
      throw new Error('Invalid form');
    }

    this.form.addEventListener('submit', this.handler.bind(this));
  }

  /**
   * @param {string} value
   */
  #checkValue(value) {
    if (value === '') {
      window.alert('Îπà Î¨∏ÏûêÏó¥ÏùÄ ÏïÑÏù¥ÌÖú ÏÉùÏÑ±Ïù¥ Î∂àÍ∞ÄÎä•Ìï©ÎãàÎã§.');

      return null;
    }

    return value;
  }

  #clearValue() {
    this.input.value = '';
  }

  /**
   * @param {Event} event
   */
  handler(event) {
    event.preventDefault();

    if (!this.input) {
      throw new Error('Invalid input');
    }

    const value = this.#checkValue(this.input.value);

    if (value) {
      this.callback(value);
      this.#clearValue();
    }
  }
}
